function convert_to_persian(num) {
    let number = num;
    let persian_digits = [];
    if (number !== 0) {
        while (number / 10 !== 0) {
            const digit = number % 10;
            number = Math.floor(number / 10);
            switch (digit) {
                case 0:
                    persian_digits.push("");
                    break;
                case 1:
                    persian_digits.push("");
                    break;
                case 2:
                    persian_digits.push("");
                    break;
                case 3:
                    persian_digits.push("");
                    break;
                case 4:
                    persian_digits.push("");
                    break;
                case 5:
                    persian_digits.push("");
                    break;
                case 6:
                    persian_digits.push("");
                    break;
                case 7:
                    persian_digits.push("");
                    break;
                case 8:
                    persian_digits.push("");
                    break;
                case 9:
                    persian_digits.push("");
                    break;
            }
        }
    }
    else {
        persian_digits.push("");
    }
    let final_string = "";
    persian_digits.reverse().forEach(d => {
        final_string += d;
    })
    return final_string;
}
export default convert_to_persian;